.card {
  position: relative;
  height: 50rem;
  perspective: 150rem;
  -moz-perpective: 150rem;
  width: 30rem;

  //@media (max-width: 1080px) {
  //  width: 50%;
  //}
  //@media (max-width: 1080px) {
  //  width: 50%;
  //}

  &:not(:last-child) {
    @media (max-width: 1080px) {
      margin-bottom: 5rem;
    }
  }

  &__side {
    height: 50rem;
    transition: all 0.8s ease;
    backface-visibility: hidden;
    width: 100%;
    border-radius: 3px;
    box-shadow: 0 3rem 6rem rgba($color-black, 0.3);
    overflow: hidden;

    @include respond(phone) {
      height: 50rem;
      width: 60%;
    }

    &--front {
      background-color: $color-white;
    }

    &--back {
      background-image: linear-gradient(
                      to right bottom,
                      rgba($color-primary, 0.8),
                      rgba($color-secondary, 0.7));
      transform: rotateY(180deg);
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      padding: 10rem 0;
    }
  }

  &:hover &__side--front {
    background-color: $color-primary;
    transform: rotateY(180deg);
  }

  &:hover &__side--back {
    background-color: $color-secondary;
    transform: rotateY(360deg);
  }

  .card__picture {
    height: 45%;
    background-size: cover;
    background-position: center;
    background-blend-mode: screen;
    clip-path: polygon(0 0, 100% 0, 100% 88%, 0 100%);
  }

  .card__heading {
    text-align: right;
    font-size: 2.8rem;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: $color-white;
    position: absolute;
    top: 15rem;
    right: 1rem;
    box-decoration-break: clone;

    @media (max-width: 560px) {
      font-size: 2.2rem;
    }
  }

  .card__heading-span {
    background-image: linear-gradient(
                    to right bottom,
                    rgba(198, 163, 250, .5),
                    rgba(rgba(78, 214, 231, 0.45), 0.1));
    padding: 0.8rem 1.3rem;
  }

  .card__details {

    ul {
      list-style: none;
      width: 80%;
      margin: 0 auto;
    }

    li {
      text-align: center;
      font-size: 1.5rem;
      padding: 1rem;

      &:not(:last-child) {
        border-bottom: 1px solid rgba(black, 0.08);
      }
    }
  }

  .price {
    text-align: center;
    color: white;
  }
  .price-only {
    font-size: 1.6rem;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 300;
  }
  .price-value {
    font-size: 6rem;
    font-weight: 100;
  }
}

.button-box {
  margin-top: 3rem;
  margin-bottom: 10rem;
}
.button-box-book {
  margin-top: 3rem;
  margin-bottom: 0;

  @include respond(tab-port) {
    margin-top: 0;
  }
}
.button-box-tour {
  margin-top: 3rem;
  margin-bottom: 10rem;
}

@media (max-width: 1080px) {
  .card {
    position: relative;
    height: auto;
    width: 50%;
    border-radius: 3px;
    box-shadow: 0 3rem 6rem rgba($color-black, 0.3);

    @include respond(phone) {
      width: 70%;
    }

    &:not(:last-child) {
      @media (max-width: 1080px) {
        margin-bottom: 5rem;
      }
    }
    .card &__heading {
      top: 12.5rem;
    }

    &__side {
      height: 45rem;
      width: 100%;
      box-shadow: none;

      &--front {
        background-color: $color-white;
      }

      &--back {
        background-image: linear-gradient(
                        to right bottom,
                        rgba($color-primary, .9),
                        rgba($color-secondary, .9));
        transform: rotateY(0);
        position: relative;
        padding: 0;
        clip-path: polygon(0 10%, 100% 0, 100% 100%, 0 100%);
        height: 50%;

        @include respond(phone) {
          clip-path: polygon(0 13%, 100% 0, 100% 100%, 0 100%);
        }
      }
    }

    &:hover &__side--front {
      background-color: $color-white;
      transform: rotateY(0);
    }

    &:hover &__side--back {
      transform: rotateY(0);
    }

    .price {
      margin-top: 4rem;
    }

    .price-only {
      font-size: 1.6rem;
      text-transform: uppercase;
      letter-spacing: 2px;
      font-weight: 300;
    }
    .price-value {
      font-size: 4rem;
      font-weight: 100;
    }
    .button-box-tour {
      margin-top: -.5rem;
      margin-bottom: 3rem;
    }
  }
}